:root {
    --light-bg: #f9f9f9;
    --dark-bg: #190133;
    --light-text: #f7f8fa;
    --dark-text: #100606;
    --accent: #9f6bff;
    --accent-light: hsl(261, 70%, 71%);
    --accent-transparent: rgba(159, 107, 255, 0.3);
}

.dark {
    --color: var(--light-text);
    --bg: var(--dark-bg);
}

.light {
    --bg: var(--light-bg);
    --color: var(--dark-text);
}

* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    font-family: "Encode Sans Expanded", sans-serif;
}
/* 
body {
    overflow: hidden;
} */



h1 {
    color: #9f6bff;
    color: var(--accent);
    font-size: 16px;
    font-weight: 700;
    letter-spacing: 1.4px;
    margin-bottom: 16px;
}

h2 {
    margin-bottom: 24px;
    font-size: calc(25px + 1.4vw);
    line-height: 1.1;
    font-weight: 600;
    color: var(--color);
}

p {
    font-size: 20px;
    color: var(--color);
}

.button {
    border-radius: 50px;
    background: #9f6bff;
    background: var(--accent);
    white-space: nowrap;
    padding: 14px 48px;
    color: #f7f8fa;
    color: var(--light-text);
    font-size:  16px;
    outline: none;
    border: 1px solid var(--bg);
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: all 0.5s ease-in-out;
    font-weight: 700;
    max-width: 200px;
    text-decoration: none;
}

.button:hover {
    transition: all 0.2s ease-in-out;
    background: var(--bg);
    color: #9f6bff;
    color: var(--accent);
    border-color: #9f6bff;
    border-color: var(--accent);
}

.modal-container {
    position: fixed;
    height: 100vh;
    width: 100vw;
    top: 0;
    left: 0;
    background-color: rgba(0, 0, 0, 0.6);
    z-index: 100;
    
    display: flex;
    justify-content: center;
    align-items: center;
}

.modal {
    background-color: var(--bg);
    padding: 40px;
    border-radius: 5px;
    max-width: 80%;
}

.modal h3 {
    font-size: 25px;
}

.modal p {
    margin: 40px 0px;
}

.modal .button {
    width: 100%;
    border-radius: 10px;
    max-width: initial;
}


@media (max-width: 600px) {
    .modal {
        padding: 20px;
    }

    .modal h3 {
        font-size: 20px;
    }

    .modal p {
        font-size: 17px;
    }
}
#gallery {
    display: flex;
    justify-content: center;
    align-items: center;
    grid-gap: 20px;
    gap: 20px;
    background-color: var(--bg);
}

#gallery #left {
    width: 960px;
    max-width: 70%;
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(220px, 1fr));
    grid-gap: 20px;
    gap: 20px;
    padding: 30px;

    --delay: 300ms;
}

#gallery #left img {
    max-width: 100%;
    border-radius: 5px;
    opacity: 0;
    -webkit-filter: blur(5px);
            filter: blur(5px);
    -webkit-transform: translateX(-100%);
            transform: translateX(-100%);
    transition: all 0.5s;
}

#gallery #left.visible img {
    opacity: 1;
    -webkit-filter: blur(0);
            filter: blur(0);
    -webkit-transform: translateX(0);
            transform: translateX(0);
}

#gallery #left.visible img:nth-child(1) {
    transition-delay: calc(1 * var(--delay));
}

#gallery #left.visible img:nth-child(2) {
    transition-delay: calc(2 * var(--delay));
}

#gallery #left.visible img:nth-child(3) {
    transition-delay: calc(3 * var(--delay));
}

#gallery #left.visible img:nth-child(4) {
    transition-delay: calc(4 * var(--delay));
}

#gallery #left.visible img:nth-child(5) {
    transition-delay: calc(5 * var(--delay));
}

#gallery #left.visible img:nth-child(6) {
    transition-delay: calc(6 * var(--delay));
}



#gallery #right {
    width: calc(100vw - 1060px);
    min-width: calc(30vw - 50px);
}

#gallery #right h2 {
    margin-bottom: 80px;
}



@media (max-width: 600px) {
    #gallery #left {
        grid-template-columns: 1fr 1fr;
        grid-gap: 10px;
        gap: 10px;
    }
}


@media screen and (max-width: 850px) {
    #gallery {
        flex-direction: column-reverse;
    }

    #gallery #left {
        width: 100%;
        max-width: initial;
        padding: 30px 20px;
    }

    #gallery #right {
        width: initial;
        min-width: initial;
        padding: 50px 0px;
    }
}
#about {
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: var(--bg);
    padding: 100px 30px;
}

#about #left {
    max-width: min(500px, 50%);
}

#about #right {
    max-width: 550px;
}

#about img {
    max-width: 100%;
}

#about #left p {
    margin: 40px 0px 80px 0px;
}


@media screen and (max-width: 850px) {
    #about {
        flex-direction: column-reverse;
    }
    
    #about #right {
        max-width: 450px;
    }
    
    #about #left {
        max-width: initial;
        padding: 50px 20px 0px;
    }
    
    #about #left .button {
        margin: 0px auto;
    }
}
#contact {
    display: flex;
    align-items: center;
    justify-content: center;
    grid-gap: 50px;
    gap: 50px;
    padding: 100px 30px;
    background-color: var(--bg);
}

#contact #left {
    max-width: 700px;
    width: 100%;
    min-width: 400px;
}

#contact #right {
    max-width: 400px;
}

#contact img {
    max-width: 100%;
}

#contact form {
    background-color: var(--bg);
    padding: 40px;
    margin: 50px 0px;
    border-radius: 10px;
}

#contact label {
    color: var(--color);
}

#contact input, #contact textarea {
    width: 100%;
    border-radius: 5px;
    height: 50px;
    margin-bottom: 30px;
    margin-top: 10px;
    padding: 10px;
}

#contact textarea {
    height: 130px;
}

#contact button {
    width: 100%;
    border-radius: 10px;
    max-width: initial;
}

@media screen and (max-width: 850px) {
    #contact {
        flex-direction: column-reverse;
    }
    
    #contact #right {
        max-width: 300px;
    }
    
    #contact #left {
        max-width: initial;
        min-width: initial;
        padding: 50px 0px 0px 0px;
    }

    #contact form {
        padding: 20px;
    }

    #contact input {
        margin-bottom: 20px;
    }
    
    #contact #left .button {
        margin: 0px auto;
    }
}
* { 
    -webkit-transform-style: preserve-3d;
    transform-style: preserve-3d;
    -moz-transform-style: initial;
}

html {
    overflow: hidden;
}

body {
    overflow: hidden auto;
    -webkit-perspective: 1px;
    perspective: 1px;
    width: 100vw;
    height: 100vh;
}

#parallax {
    height: 100vh;
    width: 100vw;

    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;

    padding: 0px 20px;
}

#parallax::after {
    position: absolute;
    top: 0;
    left: 0;
    height: 100vh;
    width: 100vw;
    -webkit-perspective: 1px;
            perspective: 1px;
    -webkit-transform: translateZ(-2px) scale(3);
            transform: translateZ(-2px) scale(3);
    -moz-transform: none;
    z-index: -1;
    
    background-image: url(/static/media/board.092ad8fd.jpg);
    background-attachment: fixed;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;

    content: "";
}



#parallax #content {
    max-width: 900px;
    border-radius: 10px;
    padding: 40px;
    background-color: #00000077;
    color: var(--light-text);
}

#parallax #content h1 {
    color: var(--accent-light);
}

#parallax #content p {
    text-align: justify;
}

@media (max-width: 550px) {
    #parallax #content p {
        font-size: 17px;
    }
}
#social {
    background-color: var(--bg);
    padding: 50px max(20px, calc((100vw - 1100px)/2));
}

#social #videos {
    display: flex;
    padding: 20px 0px;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    grid-gap: 30px;
    gap: 30px;
}

#social #videos iframe {
    border-radius: 10px;
    --width: calc(200px + 100vw / 5);

    width: var(--width);
    height: calc(var(--width) / 1.6); 
}



@media (max-width: 850px) {
    #social #videos {
        flex-direction: column;
    }

    #social #videos iframe {
        --width: min(500px, 90vw);
    }
}
#gallery-page {
    background-color: var(--bg);
}

#gallery-page #container {
    margin: 0px auto;
    max-width: 1200px;
    padding: 80px 20px;
}

#gallery-page #grid {
    -webkit-columns: 3;
            columns: 3;
    grid-gap: 10px;
    gap: 10px;
}

#gallery-page #grid img {
    width: 100%;
    padding: 5px 0px;
}
#team-page {
    background-color: var(--bg);
    color: var(--color);
}

#team-page #container {
    margin: 0px auto;
    max-width: 1000px;
    padding: 120px 20px;
}




#team-page #nav {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    grid-gap: 40px;
    gap: 40px;
    margin: 60px 0px;
}

#team-page #nav .team-nav-item {
    cursor: pointer;
    padding: 10px 30px;
    border: 2px solid var(--accent);
    border-radius: 10px;
}

#team-page #nav .team-nav-item#active {
    background-color: var(--accent-transparent);
}

#team-page #nav .team-nav-item::after {
    content: "";
    display: block;
    width: 0%;
    height: 4px;
    background-color: var(--accent);
    position: absolute;
    top: calc(100% - 4px);
    left: 0;
    border-radius: 0px 0px 10px 10px;
    transition: all 0.4s;
}

#team-page #nav .team-nav-item:hover::after {
    width: 100%;
}

#team-page #nav .team-nav-item#active::after {
    width: 0%;
}



@media (max-width: 670px) {
    #team-page #nav {
        grid-gap: 20px;
        gap: 20px;
        font-size: 14px;
    }
}

@media (max-width: 400px) {
    #team-page #nav {
        grid-gap: 10px;
        gap: 10px;
        font-size: 12px;
    }
}







#team-page .section {
